.card-container-holder {
    width: 350px;
    height: 400px;
    margin: 20px;
    perspective: 1100px;
  }
  
  .card-container {
    width:350px;
    height: 100%;
    transform-style: preserve-3d;
    cursor: pointer;
    position: relative;
    border-radius: 8px;
    box-shadow: 0 0 20px rgba(30, 30, 30, 0.2);
    transition: box-shadow 0.3s, transform 1s;
  }
  
  .card-container.is-flipped {
    transform: rotateY(180deg);
  }
  
  .card-face {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 8px;
    background: #fff;
  }
  
  .front-card-container {
    z-index: 1;
    transform: rotateY(0deg);
  }
  
  .back-card-container {
    transform: rotateY(180deg);
  }
  
  .card-container:hover {
    box-shadow: 0 0 20px rgba(30, 30, 30, 0.5);
  }
  
  .card-container .alert-bar {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px 8px 0 0;
    color: white;
  }
  
  .card-container .alert-bar.danger {
    background: #e02041;
  }
  
  .card-container .alert-bar.warning {
    background: #ffda21;
  }
  
  .card-container .alert-bar.normal {
    background: #999999;
  }
  
  .card-container .alert-bar.disabled {
    background: #cccccc;
  }
  
  .card-container .alert-bar.expired {
    background: #333333;
  }
  
  .card-container .content {
    width: 100%;
    height: 100%;
    padding: 14px 32px;
  }
    
  .card-container .content h1 {
    text-align: center;
    margin-top: 8px;
    margin-bottom: 28px;
    width: 80%;
  }
  
  .card-container .card-face h1 {
    cursor: pointer;
  }
  
  .card-container .card-face .time-label {
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 12px;
  }
  
  .card-container .card-face .button {
    width: 40px;
    height: 40px;
    position: absolute;
    bottom: 0px;
    right: 0px;
    border-radius: 8px 0 8px 0;
  }
  
  .card-container .card-face .delete {
    width: 40px;
    height: 40px;
    position: absolute;
    bottom: 0px;
    left: 0px;
    border-radius: 0 8px 0 8px;
  }
  
  .card-container .content .sensor-info {
    display: flex;
    font-size: 15px;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;    
    height: 13px;
    width: 290px;
  }
  
  .card-container .content .risk-info .info-icon{
    align-items: left;
    justify-content: left;
    margin-top: 1.5px;
    color: gray;
  }
  
  .card-container .content .risk-info{
    margin-top: -22px;
    display: flex;
    flex-direction: row;
    justify-content: center;  
  
  }
  .card-container .content .risk-info .high-risk{
    display: flex;
    background-color: red;
    color: white;
    height: 20px;
    width: 85px;
    border-radius: 10px;
    align-items: center;
    font-size: 15px; 
    margin: 2px 5px 2px 2px;
    justify-content: center;  
  }
  
  .card-container .content .risk-info .medium-risk{
    display: flex;
    background-color: yellow;
    color: black;
    height: 20px;
    width: 90px;
    border-radius: 10px;
    align-items: center;
    font-size: 15px; 
    margin: 2px 5px 2px 2px;
    justify-content: center;  
  }
  
  .card-container .content .risk-info .low-risk{
    display: flex;
    background-color: green;
    color: white;
    height: 20px;
    width: 90px;
    border-radius: 10px;
    align-items: center;
    font-size: 15px; 
    margin: 2px 5px 2px 2px;
    justify-content: center;  
  }
  .sensor-statistics-table {
    position: absolute;
    left: 50%;
    margin-top: -10px;
    transform: translateX(-50%);
  }
  
  .sensor-unit-col {
    text-align: left;
  }
  
  .sensor-data-col {
    text-align: center;
    padding: 5px;
  }
  